export default {
	data() {
		return {
			anchorIndex: '1',
			isShow: true,
			url: '../../../static/mine_icon_head portrait.png',
			//个人信息
			meansList: [
				{
					username: '陈经理',
					age: '34岁',
					mianmao: '党员',
					study: '本科',
					nation: '汉族',
					society: '无',
					weixin: '123456',
					phone: '123456',
					email: '123456@qq.com'
				}
			],
			//企业信息
			manageList: [
				{
					name: '郑州星海科技有限公司',
					zhiwu: '经理',
					address: '高新区',
					xingzhi: '民营',
					leibie: '房地产业'
				}
			],
			list: [
				{ status: '主营业务', id: 1 },
				{ status: '公司产品', id: 2 },
				{ status: '企业信息', id: 3 },
				{ status: '企业相册', id: 4 }
			]
		}
	},
	methods: {
		//点击按钮变色
		btnClick(e) {
			if(e.target.dataset.index != 2 ){
			    this.anchorIndex = 1;
			}else{
			    //获取当前点击事件的下标
			    this.anchorIndex =  e.target.dataset.index
			}
			if (e.target.dataset.index == 2) {
				this.isShow = false
			} else {
				this.isShow = true
			}
		},
		navck(id) {
			if (id == 1) {
				uni.navigateTo({
					url: '/pages/myhome/means/firm'
				})
			} else if (id == 3) {
				uni.navigateTo({
					url: '/pages/myhome/means/manage'
				})
			} else if (id == 4) {
				uni.navigateTo({
					url: '/pages/myhome/means/album'
				})
			} else if (id == 2) {
				uni.navigateTo({
					url: '/pages/myhome/means/product'
				})
			}
		},
		// 修改头像
		operator() {
			// const globalUser = this.getGlobalUser("globalUser");
			uni.showActionSheet({
				itemList: ["查看我的头像", "从相册选择上传"],
				success: res => {
					const index = res.tapIndex;
					if (index == 0) {
						// 预览头像
						let faceArr = [];
						faceArr.push(globalUser.faceImage);
						uni.previewImage({
							urls: faceArr,
							current:faceArr[0]
						})
					} else if (index == 1) {
						// 选择上传头像
						uni.chooseImage({
							count:1,
							sizeType:["compressed"],
							sourceType: ["album"],
							success: res => {
								// 获得临时路径
								const tempFilePath =  res.tempFilePaths[0];
								uni.navigateTo({
									url: "../meFace/meFace?tempFilePath=" + tempFilePath
								});
								uni.navigateTo({
									url: "../faceCrop/faceCrop?tempFilePath=" + tempFilePath
								});
							}
						});
					}
				}
			})
		},
	}
}
